import { React, useState   } from 'react';

import {
    MDBNavbar,
    MDBContainer,
    MDBNavbarBrand,
    MDBNavbarToggler,    
    MDBIcon,
    MDBCollapse,
    MDBNavbarNav,
    MDBNavbarItem,
    MDBNavbarLink
} from 'mdb-react-ui-kit'



function  NavbarProject()  {

    const [showNavColor, setShowNavColor] = useState(false);
    const [showNavRight, setShowNavRight] = useState(false);

    return(
        <div>
            <nav class="navbar navbar-expand-lg navbar-light bg-light">

            <div class="container-fluid">

                <button
                class="navbar-toggler"
                type="button"
                data-mdb-toggle="collapse"
                data-mdb-target="#navbarSupportedContent"
                aria-controls="navbarSupportedContent"
                aria-expanded="false"
                aria-label="Toggle navigation"
                >
                <i class="fas fa-bars"></i>
                </button>

                <div class="collapse navbar-collapse" id="navbarSupportedContent">

                <a class="navbar-brand mt-2 mt-lg-0" href="/">
                    LLsoLLu
                </a>

                <ul class="navbar-nav me-auto mb-2 mb-lg-0">
                    <li class="nav-item">
                    <a class="nav-link" href="/Projects/overview">Overview</a>
                    </li>
                    <li class="nav-item">
                    <a class="nav-link" href="/Projects/create">Projects</a>
                    </li>
                    <li class="nav-item">
                    <a class="nav-link" href="#billing">Billing</a>
                    </li>
                </ul>
                </div>

                <div class="d-flex align-items-center">


                <div class="dropdown">
                    <a
                    class="text-reset me-3 dropdown-toggle hidden-arrow"
                    href="#"
                    id="navbarDropdownMenuLink"
                    role="button"
                    data-mdb-toggle="dropdown"
                    aria-expanded="false"
                    >
                    <i class="fas fa-bell"></i>
                    <span class="badge rounded-pill badge-notification bg-danger">1</span>
                    Notice
                    </a>
                    <ul
                        class="dropdown-menu dropdown-menu-end"
                        aria-labelledby="navbarDropdownMenuLink"
                    >
                    <li>
                        <a class="dropdown-item" href="#">Some news</a>
                    </li>
                    <li>
                        <a class="dropdown-item" href="#">Another news</a>
                    </li>
                    <li>
                        <a class="dropdown-item" href="#">Something else here</a>
                    </li>
                    </ul>
                </div>
                <div class="dropdown">
                    <a
                    class="dropdown-toggle d-flex align-items-center hidden-arrow nav-link"
                    href="/MyInfo"
                    id="navbarDropdownMenuAvatar"
                    role="button"
                    data-mdb-toggle="dropdown"
                    aria-expanded="false"     
                    >
                    <i class="fa fa-user-cog fa-fw"></i>                                   
                    
                    My
                    </a>
                    <ul
                        class="dropdown-menu dropdown-menu-end"
                        aria-labelledby="navbarDropdownMenuAvatar"
                    >
                    <li>
                        <a class="dropdown-item" href="#profile">My profile</a>
                    </li>
                    <li>
                        <a class="dropdown-item" href="#">Settings</a>
                    </li>
                    <li>
                        <a class="dropdown-item" href="#">Logout</a>
                    </li>
                    </ul>
                </div>
                </div>
            </div>

            </nav>


        </div>

    )
};

export default NavbarProject;


