import React, {useState, useEffect} from 'react';
import styles from "./MainContents.module.css";
import Movie from "./Movie"



function MainContents(){
    const [loading, setLoading] = useState(true);
    const [movies, setMovies] = useState([]);
    //const [error, setError] = useState(null);
    useEffect( ()=> {
      fetch( `https://yts.mx/api/v2/list_movies.json?minumum_rating=7.0&sort_by=score`)
      .then((response)=> response.json() )
      .then((json)=>{ 
        setMovies(json.data.movies); 
        setLoading(false);
      })
      /*
      .catch( err => {
        console.log("ERROR");
        //setError( err.message );
      })
      */
      }, [] );
    console.log(movies);

   
    return (        
        <div className={styles.container}>        
        {loading? <div className={styles.movies} align="center"> <i class="fa fa-spinner fa-pulse fa-5x fa-fw"> </i></div> : 
          <div className={styles.movies}>
            {movies.map( (movie)=> ( 
               <div className={styles.movie}>
               <img src={movie.medium_cover_image} alt={movie.title} className={styles.movie__img} />
               <h2 className={styles.movie__title}>                   
                   <a href ="/movie/${movie.id}"> {movie.title} </a>
               </h2>
               <h3 className={styles.movie__year}>{movie.rating}</h3>
               <h3 className={styles.movie__year}>{movie.year}</h3>
               <p>{movie.summary.length > 235 ? `${movie.summary.slice(0, 235)}...` : movie.summary}</p>
               <ul className={styles.movie__genres}>
                  {movie.genres.map( (g)=>( 
                      <li key={g}>  {g} </li>
                   ))}
               </ul>
           </div>
             ))} 
          </div> }
        </div>    
    );

}

export default MainContents;


