import React, {useState} from 'react';
import {SERVER_URL} from '../components/constants.js';

import { useNavigate, Navigate } from "react-router-dom";

import { styled } from '@mui/material/styles';
import Button from '@mui/material/Button'
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel'
import Select from '@mui/material/Select'
import MenuItem from '@mui/material/MenuItem'


import Navbar from '../components/NavbarProject'
import Footer from '../components/Footer'
import { Repeat, SportsRugbySharp } from '@mui/icons-material';
import shadows from '@mui/material/styles/shadows.js';


const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'left',
  color: theme.palette.text.secondary,
  
}));


function ConnectProject(){


    const [isSetup, setBeSetup ] = useState(false);

    const [setup, setSetup ] = useState({
        domainurl: '',
        sourcelanguage: '',
        targetlanguage: '',
        urltype: ''
    });

    const handleChange=(event)=>{
        setSetup({...setup,
        [event.target.name]: event.target.value});
    }    

    const setupProject =() =>{

        console.log(setup);

        setBeSetup(true);

        /*
        fetch(SERVER_URL+'setup', {
            method: 'POST',
            headers: { 'Content-Type':'application/json'},
            body: JSON.stringify(setup)
        })
        .then(res=>{
            
        })
        .catch(err=>console.error(err));
        */
    }

    if( isSetup ){
        
        return <Navigate replace to="/Projects/dns" />              
        

    }
    else{

        return (
            <div>
                <Navbar/>

                <Box sx={{ width: '50%'   } }  >
                <Grid container  direction={"column"} rowSpacing={2} gridTemplateColumns={"Repeat(5, 1fr)"} >
                    <Grid item xs={100} lg >
                    <Item>Project created </Item>
                    </Grid>
                    <Grid item xs={6}>
                    <Item> <h4> 1. Setup </h4>
                        <h6> Welcome to LLsoLLu ezWEB! Make your website multilingual in a few minutes by following this quick start. </h6>
                        
                        <Grid container spacing={2} >
                            <Grid item xs={6}>
                                <Item> <h6>Domain URL</h6> Enter your website's domain name</Item>
                            </Grid>
                            <Grid item xs={6} boxShadow={ "2px 2px 2px 1px rgba(0,0,255, .1)"} >
                                
                                <TextField fullWidth
                                    name="domainurl"
                                    label="DOMAIN URL"
                                    placeholder='example.com'
                                    variant="standard"
                                    onChange={handleChange}  
                                />  
                            </Grid>
                            <Grid item xs={6}>
                                <Item> <h6>Original Language</h6> What is the original(current)Language of your website?</Item>
                            </Grid>
                            <Grid item xs={6} boxShadow={ "2px 2px 2px 1px rgba(0,0,255, .1)"} >
                                
                                <FormControl  fullWidth  >
                                <InputLabel id="source-language-select-label">SOURCE LANGUAGE</InputLabel>
                                <Select               
                                    name="sourcelanguage"      
                                    labelId="source-language-select-label"
                                    id="source-language-select"
                                    //value={age}
                                    label="SOURCE LANGUAGE"
                                    placeholder='Select a language'
                                    variant="standard"
                                    onChange={handleChange}
                                >
                                    <MenuItem value={'Korean'}>Korean</MenuItem>
                                    <MenuItem value={'English'}>English</MenuItem>
                                    <MenuItem value={'Japanese'}>Japanese</MenuItem>
                                    <MenuItem value={'Chinese'}>Chinese</MenuItem>
                                </Select>
                                </FormControl>
                                
                            </Grid>
                            <Grid item xs={6}>
                                <Item> <h6>Translated Language</h6> Select languages you want to translate info.</Item>
                            </Grid>
                            <Grid item xs={6} boxShadow={ "2px 2px 2px 1px rgba(0,0,255, .1)"} >
                                
                                <FormControl fullWidth  >
                                <InputLabel id="target-language-select-label">TARGET LANGUAGE</InputLabel>
                                <Select                     
                                    name="targetlanguage"
                                    labelId="target-language-select-label"
                                    id="target-language-select"
                                    //value={age}
                                    label="TARGET LANGUAGE"
                                    placeholder='Select a language'
                                    variant="standard"
                                    onChange={handleChange}
                                >
                                    <MenuItem value={'English'}>English</MenuItem>
                                    <MenuItem value={'Japanese'}>Japanese</MenuItem>
                                    <MenuItem value={'Chinese'}>Chinese</MenuItem>
                                </Select>
                                </FormControl>
                                
                            </Grid>
                            <Grid item xs={6}>
                                <Item> <h6>URL Type</h6> Choose your URL structure</Item>
                            </Grid>
                            <Grid item xs={6} boxShadow={ "2px 2px 2px 1px rgba(0,0,255, .1)"} >
                                
                                <FormControl  fullWidth  >
                                <InputLabel id="url-type-select-label">URL TYPE</InputLabel>
                                <Select           
                                    name="urltype"          
                                    labelId="url-type-select-label"
                                    id="url-type-select"
                                    //value={age}
                                    label="URL TYPE"
                                    placeholder='Select a language'
                                    variant="standard"
                                    onChange={handleChange}                                
                                >
                                    <MenuItem value={'Subdomains'}>Subdomains</MenuItem>
                                    <MenuItem value={'Subdirectories'}>Subdirectories(Beta)</MenuItem>
                                    
                                </Select>
                                </FormControl>
                                
                            </Grid>
                        </Grid>

                        <Button variant="contained" align="right"
                            onClick= {setupProject}                        
                        > Next </Button>




                    </Item>
                    </Grid>
                    <Grid item xs={6}>
                    <Item>2. Your DNS settings</Item>
                    </Grid>
                    <Grid item xs={6}>
                    <Item>3. Confirmation</Item>
                    </Grid>
                    <Grid item xs={6}>
                    <Item>4. Done</Item>
                    </Grid>
                </Grid>
                </Box>

                
                
            </div>
        )
        }
}

export default ConnectProject;