
// https://github.com/KoderLabs/react-multilevel-sidebar/blob/master/README.md


import React, { Component } from "react";
import MultilevelSidebar from "react-multilevel-sidebar";
import "react-multilevel-sidebar/src/Sidebar.css";

let options = [
  {
    title: "Translation",
    titleIcon: <i className="fa fa-graduation-cap"></i>,
    content: [{ id: 1, name: "Languages", to: "/languages" },
        { id: 2, name: "URLs", to: "/urls" },
        { id: 3, name: "Visual Editor", to: "/visual_editor" },
        { id: 4, name: "Glossary", to: "/glossary" },
        { id: 5, name: "Professional Translations", to: "/professional_translation" },
        { id: 6, name: "URL Slugs", to: "/url_slugs" }
    ]    
  },
  {
    title: "Statistics",
    titleIcon: <i className="fa fa-paragraph"></i>,
    hideBorder: true,
    content: [
      {
        id: 7,
        name: "Web Development",
        icon: <i className="fa fa-chrome"></i>,
        children: [
          {
            title: "JavaScript",
            titleIcon: <i className="fa fa-opera"></i>,
            content: [
              {
                id: 8,
                name: "functions",
                ["Some property i need on clicking this"]: "value"
              }
            ]
          }
        ]
      }
    ]
  }
];

class MyComponent extends Component {
  constructor() {
    super();
    this.state = {
      isOpen: true
    };
  }

  //   you can also use this function on any of your event to open/close the sidebar
  handleSidebarToggle = isOpen => {
    this.setState({ isOpen });
  };

  handleClick = itemOptions => {
    /* 
        do something with the item you clicked.
        you can also send custom properties of your choice
        in the options array you'll be getting those here
        whenever you click that item
    */
  };

  render() {
    return (
      <div>
        <MultilevelSidebar
          open={this.state.isOpen}
          onToggle={this.handleSidebarToggle}
          options={options}
          header="Web Translation"
          onItemClick={this.handleClick}
        />
        {/* using in our button to open the sidebar */}
        <button onClick={() => this.handleSidebarToggle(true)}>메뉴 펼치기</button>
      </div>
    );
  }
}

export default MyComponent;
