import React, {useState} from 'react';
import {SERVER_URL} from '../components/constants.js';


import { useNavigate, Navigate } from "react-router-dom";


import Navbar from '../components/NavbarProject'

import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Stack from '@mui/material/Stack';
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel'
import Select from '@mui/material/Select'
import MenuItem from '@mui/material/MenuItem'


function CreateProject(){

    const [ project, setProject ] = useState({
        projectname:'',
        websitetype:''
    });

    const [isOpened , setOpen] = useState(false);   


    const handleChange=(event)=>{
        setProject({...project,
        [event.target.name]: event.target.value});
    }

    const projectinfo = () =>{
        console.log( project );

        //ToDo...
        setOpen(true);

        console.log( isOpened );

        
    }

    if( isOpened ){
        return <Navigate replace to="/Projects/connect" />    
    }
    else{
        return(
            <div>
                <Navbar/>
                <h2>
                    Create a project
                </h2>
                <h6>
                    You are about to make your website multilingual!
                </h6>
                <h6>
                    Give a name to your project (you can change it later) and select your website technology.
                    The website technology is the specific CMS you used to build your website, or select other if you didn’t use one or if it’s not in the list.
                </h6>
    
                <Stack direction='row' spacing={3} alignItems='left' mt={2} >
                    <TextField
                        name="projectname"
                        label="PROJECT NAME"
                        placeholder='Enter your project name'
                        onChange={handleChange}   
                    />

                    <FormControl   >
                    <InputLabel id="demo-simple-select-label">WEBSITE TECHNOLOGY</InputLabel>
                    <Select                     
                        labelId="website-type-select-label"
                        id="website-type-select"
                        //value={age}
                        label="WEBSITE TECHNOLOGY"
                        placeholder='Select your website technology'
                        onChange={handleChange}
                    >
                        <MenuItem value={'WordPress'}>WordPress</MenuItem>
                        <MenuItem value={'Wix'}>Wix</MenuItem>
                        <MenuItem value={'Other'}>Other</MenuItem>
                    </Select>
                    </FormControl>
                    
                    
                    <Button
                        variant="outlined"
                        color="primary"
                        onClick={projectinfo}
                        
                        >Next
                    </Button>
                </Stack>

            </div>
        )
    }
}

export default CreateProject;