import React, {useState} from 'react';

import {Routes, Route, useNavigate, Navigate} from 'react-router-dom';

import {SERVER_URL} from './constants.js';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Stack from '@mui/material/Stack';
import Snackbar from '@mui/material/Snackbar'



function Signin(){

    const [user, setUser ] = useState({
        username: '',
        password: ''
    });
    
    const [isValidMail, setValidMail] = useState(false);

    const [open, setOpen] = useState(false);   


    const handleChange=(event)=>{
        setUser({...user,
        [event.target.name]: event.target.value});
    }

    const checkEmail = ( ) =>{
        var reg_email = /^([0-9a-zA-Z_\.-]+)@([0-9a-zA-Z_-]+)(\.[0-9a-zA-Z_-]+){1,2}$/;
        if(!reg_email.test(user.username)){
            setValidMail(false);
            setOpen(true);
            console.log("Invalid Mail Addr.")
        }
        else {
            setValidMail(true);
            setOpen(false);
            console.log("valid Mail Addr.")

            fetch(SERVER_URL+'signin',{
                method: 'POST',
                headers: { 'Content-Type': 'application/json'},
                body: JSON.stringify(user)
            })
            .then( res=>{
                console.log("[SIGNIN] GET RES....")
            })
        }
    }




    if( isValidMail ){
        return(
            <Navigate replace to="/login" />
        )

    }
    else{

        return(
            <>
            
            <h4 align="center">Create an account</h4>
            <h6 align="center">Create your ezWEB account in a few minutes.</h6>
            <Stack spacing={2} alignItems='center' mt={2}>
                <TextField
                    name="username"
                    label="Email Address"
                    placeholder='이메일 주소'
                    onChange={handleChange}   />

                <TextField
                    type="password"
                    name="password"
                    label="Password"
                    placeholder='비밀번호'
                    onChange={handleChange}  />
                
                <Button variant="outlined" 
                onClick= {checkEmail}                        
                > Sign Up </Button>
            
            </Stack>
            <Snackbar 
                open={open}
                anchorOrigin={{ horizontal: 'center', vertical: "top"}}
                autoHideDuration={1000}
                onClose={()=>setOpen(false)}
                message="Invalid e-mail adress. Check your email address."
            />
            </>
        )
    }
}

export default Signin;