
import React, {useState} from 'react';
import {SERVER_URL} from '../components/constants.js';

import { styled } from '@mui/material/styles';
import Button from '@mui/material/Button'
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel'
import Select from '@mui/material/Select'
import MenuItem from '@mui/material/MenuItem'


import Navbar from '../components/NavbarProject'
import Footer from '../components/Footer'
import { Repeat, SportsRugbySharp } from '@mui/icons-material';
import shadows from '@mui/material/styles/shadows.js';

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';



const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'left',
  color: theme.palette.text.secondary,
  
}));

function createData(
    name: string,
    type: string,
    value: String
){
    return {name, type, value};
}

// Table Value....
const rows =[
    createData('en', 'cname', "1234abcd.translate-cf.llsollu.io")
];


function checkDNSinfo(){

    console.log("in checkDNSinfo")

    const ping = (url, timeout = 6000) => {
        return new Promise((resolve, reject) => {
          const urlRule = new RegExp('(https?|ftp|file)://[-A-Za-z0-9+&@#/%?=~_|!:,.;]+[-A-Za-z0-9+&@#/%=~_|]');
          console.log(urlRule);
          console.log(url);
          if (!urlRule.test(url)) reject('invalid url');
          try {
            fetch(url)
              .then(() => resolve(true))
              .catch(() => resolve(false));
            setTimeout(() => {
              resolve(false);
            }, timeout);
          } catch (e) {
            reject(e);
          }
        });
      };

    
    let retValue= false;

    ping('http://springboot.llsollu.io')
    .then(
        res=>{ 
            console.log(res)
            retValue = res
        }
    )
    .catch(e=>console.log(e))

    return retValue;
}


function DnsProject(){

    
    return (
        <div>  
            <Navbar/> 
            <Box sx={{ width: '80%'   } }  >
                <Grid container direction={"column"} rowSpacing={2}  >
                    <Grid item xs={100} lg >
                        <Item> V  Project created </Item>
                    </Grid>
                    <Grid item xs={100} lg >
                        <Item> V Setup </Item>
                    </Grid>

                    <Grid item xs={6}>
                    <Item> <h4> 2. Your DNS settings </h4>
                        <h6> Enter the following entries in your DNS record. </h6>
                        <h6> You have the possibility to change the subdomain copy by clicking on it. </h6>
                        
                        <TableContainer component={Paper}>
                            <Table sx={{ minWidth: 450 }} aria-label="simple table">
                                <TableHead>
                                <TableRow>
                                    <TableCell>Name</TableCell>
                                    <TableCell align="left">Type</TableCell>
                                    <TableCell align="left">Value</TableCell>
                                </TableRow>
                                </TableHead>
                                <TableBody>
                                {rows.map((row) => (
                                    <TableRow
                                    key={row.name}
                                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                    >
                                    <TableCell component="th" scope="row">
                                        {row.name}
                                    </TableCell>
                                    <TableCell align="left">{row.type}</TableCell>
                                    <TableCell align="left">{row.value}</TableCell>
                                    </TableRow>
                                ))}
                                </TableBody>
                            </Table>
                        </TableContainer>



                        <h6> Check our DNS documentation (with videos!) or contact us at support@llsollu.com if you need further help. </h6>
                        <h6> Once you have entered the DNS, please wait a few minutes to allow your changes to be taken into account. </h6>
                        <h6> (i) If you are not sure how to integrate DNS and want to setup subdomains later, you can skip this step. </h6>


                        <TableContainer component={Paper}>
                            <Table sx={{ minWidth: 450 }} aria-label="simple table">
                                <TableBody>
                                {rows.map((row) => (
                                    <TableRow
                                    key={row.name}
                                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                    >
                              
                                    <TableCell align="left" component="th" scope="row" >
                                        <Button variant="contained" 
                                        //onClick= {setupProject}                        
                                        > Previous </Button>
                                    </TableCell>
                                    <TableCell align="right">
                                        <Button variant="contained" 
                                        onClick= {checkDNSinfo}      
                                        align="right"                  
                                        > Check DNS </Button>

                                    </TableCell>
                                    </TableRow>
                                ))}
                                </TableBody>
                            </Table>
                        </TableContainer>                   

                       


                    </Item>
                    </Grid>


                </Grid>
            </Box>

        </div>
    )
}

export default DnsProject;