import React, {useState} from 'react';
import {SERVER_URL} from './constants.js';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Stack from '@mui/material/Stack';
//import Carlist from './Carlist';
import Snackbar from '@mui/material/Snackbar'

import {Routes, Route, useNavigate, Navigate} from 'react-router-dom';


import Paper from '@mui/material/Paper';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';


function Login(){
    

    const [user, setUser ] = useState({
        username: '',  // email address..  (string)
        password: ''
    });
    
    const [isAuthenticated, setAuth] = useState(false);

    const [open, setOpen] = useState(false);   

    const [isSigninButton, setSignin] = useState(false);
    
    
    const handleChange=(event)=>{
        setUser({...user,
        [event.target.name]: event.target.value});
    }

    const login =() =>{
        fetch(SERVER_URL+'login', {
            method: 'POST',
            headers: { 'Content-Type':'application/json'},
            body: JSON.stringify(user)
        })
        .then(res=>{
            const jwtToken = res.headers.get('Authorization');
            if( jwtToken != null ){
                console.log("save session jwt token...");
                sessionStorage.setItem("jwt", jwtToken);
                setAuth(true);
            }
            else{
                setOpen(true);
            }
        })
        .catch(err=>console.error(err));

    }

    const signin =() =>{
        setSignin(true);
    }

    if( isSigninButton ){
        return <Navigate replace to="/register" />   
    }


    if( isAuthenticated){

        //navigate('/', {replace: true});
        return <Navigate replace to="/Projects/create" />        

        return (
            <div>
                <h2> 접속됨... Routing처리되어야 함...</h2>
            </div>
        )
    }
    else{
        return (
            <div> 
                    <Stack spacing={2} alignItems='center' mt={2}>
                        <TextField
                            name="username"
                            label="Email Address"
                            placeholder='이메일 주소'
                            onChange={handleChange}   />

                        <TextField
                            type="password"
                            name="password"
                            label="Password"
                            placeholder='비밀번호'
                            onChange={handleChange}  />

                        <TableContainer component={Paper}>
                            <Table aria-label="simple table">
                                <TableBody>
                                
                                    <TableRow
                                    
                                    //sx={{ '&:last-child td, &:last-child th': { border: -1 } }}
                                    
                                    >
                            
                                    <TableCell size="small" align="center" component="th" scope="row" >
                                        <Button variant="outlined" 
                                        onClick= {signin}                        
                                        > Join </Button>
                                    </TableCell>
                                    <TableCell align="center">
                                        <Button variant="outlined" 
                                        onClick= {login}      
                                        align="right"                  
                                        > Login </Button>

                                    </TableCell>
                                    </TableRow>
                                
                                </TableBody>
                            </Table>
                        </TableContainer>               

                        
                    </Stack>
                    <Snackbar 
                        open={open}
                        anchorOrigin={{ horizontal: 'center', vertical: "top"}}
                        autoHideDuration={3000}
                        onClose={()=>setOpen(false)}
                        message="Login failed: Check your username and password"
                    />

            </div>
        )
    }    
}

export default Login;