import React from 'react';
import { createRoot } from "react-dom/client";
import './index.css';
import 'mdb-react-ui-kit/dist/css/mdb.min.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import {BrowserRouter, Route, Routes } from 'react-router-dom';
import Home from "./routes/Home"
import Product from "./routes/Product"
import Solutions from "./routes/Solutions"
import Customers from "./routes/Customers"
import Pricing from "./routes/Pricing"
import Resources from "./routes/Resources"
import Projects from "./projects/Projects"
import CreateProject from "./projects/CreateProject"
import ConnectProject from "./projects/ConnectProject"
import DnsProject from "./projects/DnsProject"
import OverviewProject from "./projects/OverviewProject"
import Singup from "./components/Signin"
import Login from "./components/Login"
import MyInfo from "./components/MyInfo"


const container = document.getElementById("root");
const root = createRoot(container);

root.render(
  <React.StrictMode>
    <BrowserRouter basename={process.env.PUBLIC_URL} >
      <Routes>              
        
        <Route path="/" element={<Home />} />
        <Route  path="/Product" element={<Product/>} /> 
        <Route  path="/Solutions" element={<Solutions/>} />        
        <Route  path="/Customers" element={<Customers/>} />        
        <Route  path="/Pricing" element={<Pricing/>} />        
        <Route  path="/Resources" element={<Resources/>} />        
        <Route  path="/Projects" element={<Projects/>} />        
        <Route  path="/Projects/create"  element={<CreateProject/>} />        
        <Route  path="/Projects/connect" element={<ConnectProject/>} />        
        <Route  path="/Projects/dns" element={<DnsProject/>} />         
        <Route  path="/Projects/overview" element={<OverviewProject/>} /> 
        <Route  path="/register" element={<Singup/>} />
        <Route  path="/login" element={<Login/>} />
        <Route  path="/MyInfo" element={<MyInfo/>} />



      </Routes>
    </BrowserRouter>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
